$numberOfColumns: 12;
$gutter: 30px;

.container{
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    padding-right: 105px;
    padding-left: 105px;
}

.container-fluid{
    @extend .container;
    width: 100%;
}

.row{
    display: flex;
    flex-wrap: wrap;
}
.col{
    flex-grow: 1;
}
[class^="col"]{
    padding-left: $gutter / 2;
    padding-right: $gutter / 2;
}

// Create the columns
@for $i from 1 through $numberOfColumns{

    .col-#{$i}{
        width: (100% / 12) * $i;
    }

}

